
.header {
  color: #fff;
  background: rgba(7,17,27,0.5);
  overflow: hidden;
  position: relative;
}
.header .content-wrapper {
  padding: 24px 12px 18px 24px;
  font-size: 0;
  position: relative;
}
.header .content-wrapper .avatar {
  margin-left: 16px;
  display: inline-block;
}
.header .content-wrapper .avatar img {
  border-radius: 2px;
}
.header .content-wrapper .content {
  display: inline-block;
  font-size: 14px;
  margin-left: 16px;
}
.header .content-wrapper .title {
  margin: 2px 0 8px 0;
}
.header .content-wrapper .title .brand {
  vertical-align: top;
  display: inline-block;
  width: 30px;
  height: 18px;
  background-image: url("brand@2x.png");
  background-size: 30px 18px;
  background-repeat: no-repeat;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .title .brand {
    background-image: url("brand@3x.png");
}
}
.header .content-wrapper .title .name {
  margin-left: 6px;
  font-size: 16px;
  line-height: 18px;
  font-weight: bold;
}
.header .content-wrapper .description {
  margin-bottom: 10px;
  line-height: 12px;
  font-size: 12px;
}
.header .content-wrapper .supports {
  font-size: 12px;
}
.header .content-wrapper .supports .icon {
  display: inline-block;
  width: 12px;
  height: 12px;
  vertical-align: top;
  margin-right: 4px;
  background-size: 12px 12px;
  background-repeat: no-repeat;
}
.header .content-wrapper .supports .icon.decrease {
  background-image: url("decrease_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .supports .icon.decrease {
    background-image: url("decrease_1@3x.png");
}
}
.header .content-wrapper .supports .icon.discount {
  background-image: url("discount_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .supports .icon.discount {
    background-image: url("discount_1@3x.png");
}
}
.header .content-wrapper .supports .icon.guarantee {
  background-image: url("guarantee_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .supports .icon.guarantee {
    background-image: url("guarantee_1@3x.png");
}
}
.header .content-wrapper .supports .icon.invoice {
  background-image: url("invoice_1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .supports .icon.invoice {
    background-image: url("invoice_1@3x.png");
}
}
.header .content-wrapper .supports .icon.tejia {
  background-image: url("tejia1@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .supports .icon.tejia {
    background-image: url("tejia1@3x.png");
}
}
.header .content-wrapper .supports .text {
  line-height: 12px;
  font-size: 10px;
}
.header .content-wrapper .suppport-count {
  position: absolute;
  right: 12px;
  bottom: 18px;
  padding: 0 8px;
  height: 24px;
  line-height: 24px;
  border-radius: 14px;
  background-color: rgba(0,0,0,0.2);
  text-align: center;
}
.header .content-wrapper .suppport-count .count {
  font-size: 10px;
  vertical-align: top;
}
.header .content-wrapper .suppport-count .icon-keyboard_arrow_right {
  font-size: 10px;
  line-height: 24px;
}
.header .bulletin-wrapper {
  height: 28px;
  line-height: 28px;
  padding: 0 22px 0 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  position: relative;
  background-color: rgba(7,17,27,0.2);
}
.header .bulletin-wrapper .bulletin-title {
  display: inline-block;
  vertical-align: top;
  width: 22px;
  height: 12px;
  background-image: url("bulletin@2x.png");
  background-size: 22px 12px;
  background-repeat: no-repeat;
  margin-top: 8px;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .bulletin-wrapper .bulletin-title {
    background-image: url("bulletin@3x.png");
}
}
.header .bulletin-wrapper .bulletin-text {
  vertical-align: top;
  font-size: 10px;
  margin: 0 4px;
}
.header .bulletin-wrapper .icon-keyboard_arrow_right {
  position: absolute;
  fon-size: 10px;
  right: 12px;
  top: 6px;
}
.header .background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.header .detail {
  position: fixed;
  z-index: 100;
  width: 100%;
  height: 100%;
  overflow: hidden;
  top: 0;
  left: 0;
  overflow: auto;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.header .detail.fade-transition {
  opacity: 1;
  background: rgba(7,17,27,0.8);
}
.header .detail.fade-enter,
.header .detail.fade-leave {
  opacity: 0;
  background: rgba(7,17,27,0);
  blur: 10px;
}
.header .detail .detail-warpper {
  min-height: 100%;
  width: 100%;
  background: rgba(7,17,28,0.8);
}
.header .detail .detail-warpper .detail-main {
  margin-top: 64px;
  padding-bottom: 64px;
}
.header .detail .detail-warpper .detail-main .detail-title {
  width: 100%;
  text-align: center;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  color: #fff;
}
.header .detail .detail-warpper .detail-main .star-warpper {
  margin-top: 18px;
  padding: 2px 0;
  text-align: center;
}
.header .detail .detail-warpper .detail-main .discountMsg {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 28px auto 24px auto;
}
.header .detail .detail-warpper .detail-main .discountMsg .lineSty {
  width: 33%;
  height: 1px;
  background-color: rgba(255,255,255,0.2);
}
.header .detail .detail-warpper .detail-main .discountMsg .msgTitle {
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  color: #fff;
  margin: 0 12px;
}
.header .detail .detail-warpper .detail-main .supports {
  width: 80%;
  margin: 0 auto;
}
.header .detail .detail-warpper .detail-main .supports .supports-item {
  padding: 0 12px;
  margin-bottom: 12px;
  font-size: 0;
}
.header .detail .detail-warpper .detail-main .supports .supports-item:last-child {
  margin-bottom: 0;
}
.header .detail .detail-warpper .detail-main .supports .supports-item .icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  vertical-align: top;
  margin-right: 6px;
  background-size: 16px;
  background-repeat: no-repeat;
}
.header .detail .detail-warpper .detail-main .supports .supports-item .icon.decrease {
  background-image: url("decrease_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail-warpper .detail-main .supports .supports-item .icon.decrease {
    background-image: url("decrease_2@3x.png");
}
}
.header .detail .detail-warpper .detail-main .supports .supports-item .icon.discount {
  background-image: url("discount_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail-warpper .detail-main .supports .supports-item .icon.discount {
    background-image: url("discount_2@3x.png");
}
}
.header .detail .detail-warpper .detail-main .supports .supports-item .icon.guarantee {
  background-image: url("guarantee_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail-warpper .detail-main .supports .supports-item .icon.guarantee {
    background-image: url("guarantee_2@3x.png");
}
}
.header .detail .detail-warpper .detail-main .supports .supports-item .icon.tejia {
  background-image: url("tejia2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail-warpper .detail-main .supports .supports-item .icon.tejia {
    background-image: url("tejia2@3x.png");
}
}
.header .detail .detail-warpper .detail-main .supports .supports-item .icon.invoice {
  background-image: url("invoice_2@2x.png");
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .detail .detail-warpper .detail-main .supports .supports-item .icon.invoice {
    background-image: url("invoice_2@3x.png");
}
}
.header .detail .detail-warpper .detail-main .supports .supports-item .text {
  line-height: 16px;
  font-size: 12px;
}
.header .detail .detail-warpper .detail-main .bulletin {
  width: 80%;
  margin: 0 auto;
  padding: 0 12px;
}
.header .detail .detail-warpper .detail-main .bulletin p {
  line-height: 24px;
  font-weight: 200;
  color: #fff;
  font-size: 12px;
}
.header .detail .detail-close {
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0 auto;
  clear: both;
  font-size: 32px;
  color: #f00;
}


