
.foods {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 48px;
  z-index: 30;
  width: 100%;
  background: #fff;
}
.foods.move-enter-active,
.foods.move-leave-active {
  -webkit-transition: all 0.5s linear;
  transition: all 0.5s linear;
}
.move-enter,
.move-leave-to {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.image-header {
  position: relative;
  height: 0;
  width: 100%;
  padding-top: 100%;
}
.image-header img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.image-header .back {
  position: absolute;
  top: 10px;
  left: 0;
}
.image-header .back .icon-arrow_lift {
  display: block;
  padding: 10px;
  color: #00a0dc;
  font-size: 20px;
}
.contents {
  position: relative;
  padding: 18px;
}
.contents .title {
  color: #07111b;
  line-height: 14px;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 700;
}
.contents .detail {
  margin-bottom: 18px;
  line-height: 10px;
  height: 10px;
  font-size: 0;
}
.contents .detail .sell-count,
.contents .detail .rating {
  font-size: 10px;
  color: #93999f;
}
.contents .detail .rating {
  margin-left: 12px;
}
.contents .price {
  font-weight: 700;
  line-height: 24px;
}
.contents .price .now {
  margin-right: 8px;
  font-size: 14px;
  color: #f01414;
}
.contents .price .old {
  text-decoration: line-through;
  font-size: 10px;
  color: #93999f;
}
.carcontrol-wrapper {
  position: absolute;
  right: 12px;
  bottom: 12px;
}
.buy {
  position: absolute;
  right: 18px;
  bottom: 18px;
  z-index: 10;
  line-height: 24px;
  height: 24px;
  padding: 0 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 10px;
  border-radius: 12px;
  color: #fff;
  background: #00a0dc;
  opacity: 1;
}
.buy.fades-enter-active,
.buy.fades-leave-active {
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}
.buy.fades-enter,
.buy.fades-leave-to {
  opacity: 0;
}
.info {
  padding: 18px;
}
.info .title {
  line-height: 14px;
  margin-bottom: 6px;
  font-size: 14px;
  color: #07111b;
}
.info .text {
  line-height: 24px;
  padding: 0 8px;
  font-size: 12px;
  color: #4d555d;
  text-indent: 28px;
}
.rating {
  padding-top: 18px;
}
.rating .title {
  line-height: 14px;
  margin-left: 18px;
  font-size: 14px;
  color: #07111b;
}
.rating .rating-wrapper {
  padding: 0 18px;
  position: relative;
}
.rating .rating-wrapper .rating-item {
  padding: 16px 0;
  position: relative;
}
.rating .rating-wrapper .rating-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.rating .rating-wrapper .rating-item .user {
  position: absolute;
  right: 0;
  top: 16px;
  font-size: 0;
  line-height: 12px;
}
.rating .rating-wrapper .rating-item .user .name {
  display: inline-block;
  vertical-align: top;
  font-size: 10px;
  margin-right: 6px;
  color: #93999f;
}
.rating .rating-wrapper .rating-item .user .avatar {
  border-radius: 50%;
}
.rating .rating-wrapper .rating-item .time {
  font-size: 10px;
  line-height: 12px;
  color: #93999f;
  margin-bottom: 6px;
}
.rating .rating-wrapper .rating-item .text {
  line-height: 16px;
  font-size: 12px;
  font-weight: 700;
  color: #07111b;
}
.rating .rating-wrapper .rating-item .text .icon-thumb_up,
.rating .rating-wrapper .rating-item .text .icon-thumb_down {
  margin-right: 4px;
  line-height: 16px;
  font-size: 12px;
}
.rating .rating-wrapper .rating-item .text .icon-thumb_up {
  color: #00a0dc;
}
.rating .rating-wrapper .rating-item .text .icon-thumb_down {
  color: #93999f;
}


